function onLogin(redirect_uri, code) {
  return (dispath, getState) => {
    return new Promise((resolve, reject) => {
      if (!redirect_uri || !code) {
        toast.error("Thng tin ng nhp khng chnh xc", {
          position: toast.POSITION.TOP_RIGHT
        });
        reject();
        return;
      }
      userProvider.login(redirect_uri, code).then(res => {
        switch (res.code) {
          case 0:
            snackbar.show(constants.text.user.success_login, "success");
            dispath(
              updateData({
                auth: res.data,
                detail: null
              })
            );
            resolve(res.data);
            return;
        }
        reject("ng nhp khng thnh cng");
      }).catch(e => {
        reject(e);
        console.log(e);
      });
    });
  };
}
function updateData(data) {
  return dispatch => {
    dispatch({
      type: "AUTH-UPDATE-DATA",
      data: data
    });
  };
}
export default {
  onLogin,
  onLogout(query) {
    return dispatch => {
      return new Promise((resolve, reject) => {
        // dispatch(updateData({
        //   data: null
        // }));
        localStorage.clear()
        window.location.href =
          clientUtils.serverApi + "/auth/logout?redirect_uri=" +
          window.location.origin +
          (query ? query : "");
        resolve();
      });
    };
  },
  updateData,
  getDetailUser
};
